import { request } from 'utils';

const prefix = '/api/education';

export const getTestPackages = ({ page, pageSize=10, orderBy, isPagingEnabled=true } = {}) =>
  request({
    prefix,
    url: '/TestPackage',
    method: 'GET',
    params: {page,pageSize,orderBy,isPagingEnabled},
    data: {}
  });

  
export const getStudentInTestPackage = ({
  packageNo,
  tOId,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
  request({
    prefix,
    url: '/TestPackage/GetStudentInTP',
    method: 'GET',
    params: {packageNo,tOId,page,pageSize,orderBy,isPagingEnabled},
    data: {}
  });


  export const createTestPackage = ({
    toId,
    packageNo,
    headSignal,
    fromNo,
    students
  } = {}) =>
    request({
      prefix,
      url: '/TestPackage',
      method: 'POST',
      params: {},
      data: {
        toId,
        packageNo,
        headSignal,
        fromNo,
        students
      }
    });


export const deleteTestPackage = ({toId,packageNo} = {}) =>
  request({
    prefix,
    url: '/TestPackage',
    method: 'DELETE',
    params: {},
    data: {toId,packageNo}
  });
    


