import { request } from 'utils';

const prefix = '/api/education';

export const getTestRoomForSelect = ({
  trainTypeId,
  facultyId,
  yearEduId,
  semesterId,
  subjectId,
  examBatch,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
    request({
    prefix,
    url: '/TestOrganizing/GetTestRoomForSelect',
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      yearEduId,
      semesterId,
      subjectId,
      examBatch,
      page,
      pageSize,
      orderBy,
      isPagingEnabled
    },
    data: {}
  });

  export const getStudentInTO = ({
    tOId,
    tORId,
    page,
    pageSize=10,
    orderBy,
    isPagingEnabled=true
  } = {}) =>
    request({
      prefix,
      url: '/TestOrganizing/GetStudentInTO',
      method: 'GET',
      params: {
        tOId,
        tORId,
        page,
        pageSize,
        orderBy,
        isPagingEnabled
      },
      data: {}
    });
  


